.background-box1 {
  position: absolute;
  left: 5px;
  right: 5px;
  top: 5px;
  bottom: 5px;
  border: 1px solid rgba(191, 191, 191, 1);
  box-shadow: 0 0 5px rgba(191, 191, 191, 1);
  z-index: 0;
}
.background-box2 {
  position: absolute;
  left: 7px;
  right: 7px;
  top: 5px;
  bottom: 5px;
  border: 1px solid rgba(191, 191, 191, 1);
  box-shadow: 0 0 5px rgba(191, 191, 191, 1);
  z-index: 1;
}
.background-box3 {
  position: absolute;
  left: 9px;
  right: 9px;
  top: 5px;
  bottom: 5px;
  border: 1px solid rgba(191, 191, 191, 1);
  box-shadow: 0 0 5px rgba(191, 191, 191, 1);
  z-index: 2;
  display: flex;
  justify-content: space-between;
  place-content: center;
  overflow: hidden;
}
@media screen and (max-width: 888px) {
  .background-box3 {
    display: none;
  }
}
.book-spine {
  float: left;
  width: 1px;
  height: 100%;
  border: 2px solid rgba(191, 191, 191, 1);
  border-left: none;
  box-sizing: border-box;
  z-index: 15;
}

